* {
  box-sizing: border-box;
}

html {
  margin: 0 !important;
}

body {
  word-wrap: break-word;
  text-rendering: optimizelegibility;
  color: #fff;
  background-color: #202b38;
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Segoe UI Emoji, Apple Color Emoji, Noto Color Emoji, sans-serif;
  font-size: 16px;
  line-height: 1.4;
}

body h1, body h2 {
  color: #fff;
}

body h1 a, body h2 a {
  color: #fff;
  transition: all .2s;
}

body a {
  color: #41adff;
  text-decoration: none;
  transition: all .2s;
}

body a:hover, body a:focus {
  color: #89cafd;
}

body h1 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 2.2rem;
  font-weight: 600;
}

body header {
  background-color: #2a3847;
  margin-bottom: 20px;
  padding: 20px;
}

body header h1, body header h2 {
  margin: 0;
  font-size: 2.2rem;
  font-weight: 600;
}

body header h1 a, body header h2 a {
  background-size: 0;
  transition: all 1s;
}

body header h1 a:hover, body header h1 a:focus, body header h2 a:hover, body header h2 a:focus {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  background-image: linear-gradient(45deg, #41adff, #fff);
  background-repeat: repeat;
  background-size: 100%;
}

body header .header {
  max-width: 840px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

body header nav {
  margin-top: 1rem;
}

body header nav ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
}

body header nav ul li {
  padding-right: 20px;
  display: inline;
}

body header nav ul li:last-child {
  padding-right: 0;
}

body header nav ul li a {
  text-align: center;
  padding: .3rem 0;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

body header nav ul li a:before {
  content: "";
  width: 0;
  height: 1px;
  background-color: #41adff;
  transition: all .2s;
  position: absolute;
  bottom: 0;
  left: 0;
}

body header nav ul li a:hover:before, body header nav ul li a:focus:before {
  width: 100%;
}

body main {
  max-width: 840px;
  margin: 0 auto;
  padding: 60px 20px;
}

@media (max-width: 840px) {
  body main {
    padding: 0 20px;
  }
}

body main img {
  max-width: 100%;
  height: auto;
}

body main p {
  color: #dbdbdb;
}

body article {
  border-bottom: 1px solid #526980;
  margin-bottom: 3rem;
  padding-bottom: 3rem;
}

body article:last-of-type {
  border-bottom: none;
  padding-bottom: 1rem;
}

body article:hover img, body article:focus img {
  filter: grayscale(0);
}

body article img {
  filter: grayscale(.2);
  transition: all .2s;
}

body article p:last-child {
  margin-bottom: 0;
}

body article .read-more {
  margin-left: .5rem;
}

body article .readmore {
  align-items: center;
  display: flex;
}

body article .readmore:after {
  content: "";
  width: 7px;
  height: 7px;
  border-bottom: 2px solid #41adff;
  border-right: 2px solid #41adff;
  margin-left: .5rem;
  transition: all .2s;
  display: inline-block;
  transform: translate(0)rotate(-45deg);
}

body article .readmore:hover:after {
  border-bottom: 2px solid #89cafd;
  border-right: 2px solid #89cafd;
  transform: translate(3px)rotate(-45deg);
}

body .details {
  margin-bottom: 2rem;
  display: flex;
}

body .details p {
  margin: 0;
}

body .details .post-date {
  color: #dbdbdb;
  margin-right: 1rem;
}

body .tags {
  margin-top: 2rem;
}

body .tags a {
  color: #41adff;
  background-color: #0b0f13;
  border-radius: 10px;
  margin-bottom: .5rem;
  margin-right: .8rem;
  padding: .2rem .8rem;
  font-size: .9rem;
  display: inline-block;
}

body .tags a:first-child {
  margin-left: 1rem;
}

body .tags a:hover, body .tags a:focus {
  color: #fff;
  background-color: #2a3847;
}

body .tags a:last-child {
  margin-right: 0;
}

body .tags a:before {
  content: "#";
}

body .author {
  border-top: 1px solid #526980;
  margin-top: 6rem;
  padding: 2rem 0 0;
}

@media (max-width: 840px) {
  body .author {
    padding-bottom: 2rem;
  }
}

body nav.pagination {
  border-top: 1px solid #526980;
  padding: 2rem 0;
}

body nav.pagination .page-numbers {
  margin-right: 1rem;
  padding: 0 .3rem;
  display: inline-flex;
}

body nav.pagination .page-numbers:last-child {
  margin-right: 0;
}

body footer {
  text-align: center;
  border-top: 1px solid #526980;
  padding: 2rem 0;
  font-size: .9rem;
}

body img + h1 {
  margin-top: 2rem;
}

body .wp-block-image {
  margin: 2rem 0;
}

body .comment-form > p {
  display: flex;
}

body .comment-form label {
  width: 30%;
  margin-right: 2rem;
}

body .comment-form .comment-form-cookies-consent label {
  width: 100%;
}

body textarea, body input[type="text"], body input[type="email"] {
  width: 100%;
  color: #fff;
  background-color: #2a3847;
  border: 1px solid #526980;
  border-radius: 3px;
  padding: .5rem;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Segoe UI Emoji, Apple Color Emoji, Noto Color Emoji, sans-serif;
  font-size: 1rem;
}

body textarea:focus, body input[type="text"]:focus, body input[type="email"]:focus {
  border: 1px solid #41adff;
  outline: none;
}

body input[type="text"] {
  height: 40px;
}

body input[type="checkbox"] {
  margin-right: .5rem;
}

body input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #2a3847;
  border: 1px solid #41adff;
  border-radius: 30px;
  margin-top: 1rem;
  padding: .8rem 1.5rem;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Segoe UI Emoji, Apple Color Emoji, Noto Color Emoji, sans-serif;
  font-size: 1rem;
  transition: all .2s;
}

body input[type="submit"]:hover, body input[type="submit"]:focus {
  background-color: #41adff;
  border: 1px solid #2a3847;
}

body .comment-respond {
  border-top: 1px solid #526980;
  padding-top: 2rem;
}

body .comment-respond h3 {
  margin-top: 0;
}

body #comments {
  border-top: 1px solid #526980;
  padding-top: 2rem;
}

body .commentlist {
  margin: 1rem 0 2rem;
  padding: 0;
  list-style: none;
}

body .commentlist > li {
  border-bottom: 1px solid #526980;
  padding: 1rem 0;
}

body .commentlist > li:last-child {
  border-bottom: 0;
  padding-bottom: 0;
}

body .commentlist .children {
  list-style: none;
}

body .comment-body {
  padding-bottom: 1rem;
}

body .comment-meta {
  padding: .5rem 0;
}

body .comment-author {
  align-items: center;
  display: flex;
}

body .comment-author img {
  border-radius: 50%;
  margin-right: 1rem;
}

body .comment-author cite {
  font-style: normal;
}

body .comment-author .says {
  margin-left: .5rem;
}

body .reply {
  margin: 1rem 0;
}

body .comment-reply-link {
  color: #fff;
  cursor: pointer;
  background-color: #2a3847;
  border: 1px solid #41adff;
  border-radius: 30px;
  margin-top: 1rem;
  padding: .5rem 1rem;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Segoe UI Emoji, Apple Color Emoji, Noto Color Emoji, sans-serif;
  font-size: .9rem;
  transition: all .2s;
}

body .comment-reply-link:hover, body .comment-reply-link:focus {
  color: #fff;
  background-color: #41adff;
  border: 1px solid #2a3847;
}

body .logged-in-as a {
  margin: 0 .2rem;
}

